import React from 'react'
import ReactDOM from 'react-dom'
import { BrowserRouter as Router, Route } from 'react-router-dom'

import './style.module.css'
import Home from './views/home'
import Privacypolicy from './views/privacypolicy'
import Ourteam from './views/ourteam'
import OurProjects from './views/our-projects'
import Howtobuy from './views/howtobuy'

const App = () => {
  return (
    <Router>
      <div>
        <Route exact component={Home} path="/" />
        <Route exact component={Privacypolicy} path="/privacypolicy" />
        <Route exact component={Ourteam} path="/ourteam" />
        <Route exact component={OurProjects} path="/our-projects" />
        <Route exact component={Howtobuy} path="/howtobuy" />
      </div>
    </Router>
  )
}

ReactDOM.render(<App />, document.getElementById('app'))
